<template>
  <div class="kol-card" @click="$emit('click')">
    <div class="card-header">
      <div class="user-info">
        <div class="user-avatar">
          {{ kol.user_tag.charAt(0).toUpperCase() }}
        </div>
        <div class="user-details">
          <span class="user-tag">@{{ kol.user_tag }}</span>
          <span class="date">{{ formatDate(kol.created_at) }}</span>
        </div>
      </div>
      <div class="price-tag" :class="priceClass">
        {{ formatPrice(kol.price) }}
      </div>
    </div>

    <div class="card-content">
      <h3 class="title">{{ kol.title }}</h3>
      <p class="description">{{ truncateText(kol.description, 120) }}</p>

      <div class="metrics">
        <div class="metric">
          <i class="fas fa-chart-line"></i>
          <span>+{{ kol.potential }}%</span>
        </div>
        <div class="metric">
          <i class="fas fa-users"></i>
          <span>{{ kol.bought_num }}</span>
        </div>
        <div class="metric">
          <i class="fas fa-thumbs-up"></i>
          <span>{{ kol.likes }}</span>
        </div>
        <div class="metric">
          <i class="fas fa-thumbs-down"></i>
          <span>{{ kol.unlikes }}</span>
        </div>
      </div>
    </div>

    <div class="card-footer">
      <div class="like-ratio">
        <div class="ratio-bar">
          <div class="ratio-fill" :style="{ width: likeRatio + '%' }"></div>
        </div>
        <span class="ratio-text">{{ likeRatio }}% positive</span>
      </div>
    </div>
  </div>
</template>

<script>
import { computed } from 'vue'

export default {
  name: 'KolCard',
  props: {
    kol: {
      type: Object,
      required: true
    }
  },
  emits: ['click'],
  setup(props) {
    const priceClass = computed(() => {
      return props.kol.price === 0 ? 'free' : 'paid'
    })

    const likeRatio = computed(() => {
      const total = props.kol.likes + props.kol.unlikes
      if (total === 0) return 0
      return Math.round((props.kol.likes / total) * 100)
    })

    const formatPrice = (lamports) => {
      if (lamports === 0) return 'Free'
      return `${(lamports / 1e9).toFixed(3)} SOL`
    }

    const formatDate = (dateString) => {
      const date = new Date(dateString)
      const now = new Date()
      const diffTime = now - date
      const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24))
      
      if (diffDays === 0) {
        const diffHours = Math.floor(diffTime / (1000 * 60 * 60))
        if (diffHours === 0) {
          const diffMinutes = Math.floor(diffTime / (1000 * 60))
          return `${diffMinutes} min ago`
        }
        return `${diffHours} h ago`
      }
      
      if (diffDays === 1) return 'yesterday'
      if (diffDays < 7) return `${diffDays} days ago`
      
      return date.toLocaleDateString('en-US', {
        day: 'numeric',
        month: 'short',
        year: date.getFullYear() !== now.getFullYear() ? 'numeric' : undefined
      })
    }

    const truncateText = (text, maxLength) => {
      if (text.length <= maxLength) return text
      return text.substring(0, maxLength) + '...'
    }

    return {
      priceClass,
      likeRatio,
      formatPrice,
      formatDate,
      truncateText
    }
  }
}
</script>

