<template>
  <div class="kol-page">
    <KolHeader 
      @search="handleSearch"
      @create="showCreateModal = true"
    />

    <div class="container">
      <KolFilters 
        @sort-change="handleSortChange"
        @clear-search="clearSearch"
        :search-query="searchQuery"
      />

      <div v-if="loading" class="loading-container">
        <div class="loading-spinner"></div>
        <p>Loading KOL signals...</p>
      </div>

      <KolGrid 
        v-else
        :kols="kols"
        :loading="loadingMore"
        @load-more="loadMore"
        @kol-click="goToKol"
      />

      <div v-if="!loading && kols.length === 0" class="empty-state">
        <i class="fas fa-chart-line"></i>
        <h3>No KOL Signals Found</h3>
        <p>Be the first to create a KOL signal!</p>
        <button @click="showCreateModal = true" class="create-btn">
          <i class="fas fa-plus"></i>
          Create Signal
        </button>
      </div>
    </div>

    <KolCreateModal 
      v-if="showCreateModal"
      @close="showCreateModal = false"
      @created="handleKolCreated"
    />
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import store from '@/vuex'
import KolHeader from '@/components/kol/KolHeader.vue'
import KolFilters from '@/components/kol/KolFilters.vue'
import KolGrid from '@/components/kol/KolGrid.vue'
import KolCreateModal from '@/components/kol/KolCreateModal.vue'
import axios from 'axios'

export default {
  name: 'KolPage',
  components: {
    KolHeader,
    KolFilters,
    KolGrid,
    KolCreateModal
  },
  setup() {
    const router = useRouter()
    
    const kols = ref([])
    const loading = ref(false)
    const loadingMore = ref(false)
    const showCreateModal = ref(false)
    const currentPage = ref(1)
    const totalPages = ref(1)
    const searchQuery = ref('')
    const sortBy = ref('created_at')
    const sortOrder = ref('DESC')

    const loadKols = async (reset = true) => {
      if (reset) {
        loading.value = true
        currentPage.value = 1
        kols.value = []
      } else {
        loadingMore.value = true
      }

      try {
        const params = {
          page: currentPage.value,
          limit: 12,
          sort: sortBy.value,
          order: sortOrder.value
        }

        if (searchQuery.value) {
          params.search = searchQuery.value
        }

        const response = await axios.get('/kol', { params })
        
        if (reset) {
          kols.value = response.data.data
        } else {
          kols.value.push(...response.data.data)
        }
        
        totalPages.value = response.data.pagination.pages
      } catch (error) {
        console.error('Error loading KOLs:', error)
      } finally {
        loading.value = false
        loadingMore.value = false
      }
    }

    const loadMore = async () => {
      if (currentPage.value < totalPages.value && !loadingMore.value) {
        currentPage.value += 1
        await loadKols(false)
      }
    }

    const handleSearch = (query) => {
      searchQuery.value = query
      loadKols()
    }

    const handleSortChange = (sort, order) => {
      sortBy.value = sort
      sortOrder.value = order
      loadKols()
    }

    const clearSearch = () => {
      searchQuery.value = ''
      loadKols()
    }

    const goToKol = (kolId) => {
      router.push(`/kol/${kolId}`)
    }

    const handleKolCreated = (newKol) => {
      kols.value.unshift(newKol)
      showCreateModal.value = false
    }

    onMounted(() => {
      loadKols()
    })

    return {
      kols,
      loading,
      loadingMore,
      showCreateModal,
      searchQuery,
      loadMore,
      handleSearch,
      handleSortChange,
      clearSearch,
      goToKol,
      handleKolCreated
    }
  }
}
</script>

