<template>
  <div class="kol-filters">
    <div class="filter-group">
      <label>Sort by:</label>
      <select v-model="selectedSort" @change="handleSortChange" class="filter-select">
        <option value="created_at|DESC">Newest First</option>
        <option value="created_at|ASC">Oldest First</option>
        <option value="likes|DESC">Most Liked</option>
        <option value="potential|DESC">Highest Potential</option>
        <option value="bought_num|DESC">Most Popular</option>
      </select>
    </div>

    <div v-if="searchQuery" class="search-info">
      <span>Search results for: <strong>{{ searchQuery }}</strong></span>
      <button @click="$emit('clear-search')" class="clear-btn">
        <i class="fas fa-times"></i>
        Clear
      </button>
    </div>
  </div>
</template>

<script>
import { ref, watch } from 'vue'

export default {
  name: 'KolFilters',
  props: {
    searchQuery: {
      type: String,
      default: ''
    }
  },
  emits: ['sort-change', 'clear-search'],
  setup(props, { emit }) {
    const selectedSort = ref('created_at|DESC')

    const handleSortChange = () => {
      const [sort, order] = selectedSort.value.split('|')
      emit('sort-change', sort, order)
    }

    return {
      selectedSort,
      handleSortChange
    }
  }
}
</script>

