<template>
  <div class="modal-overlay" @click="handleOverlayClick">
    <div class="modal-content" @click.stop>
      <div class="modal-header">
        <h2>Create KOL Signal</h2>
        <button @click="$emit('close')" class="close-btn">
          <i class="fas fa-times"></i>
        </button>
      </div>

      <form @submit.prevent="handleSubmit" class="modal-body">
        <div class="form-group">
          <label for="title">Signal Title *</label>
          <input 
            v-model="form.title"
            id="title"
            type="text" 
            placeholder="Enter signal title..."
            required
            maxlength="200"
          />
        </div>

        <div class="form-group">
          <label for="description">Description *</label>
          <textarea 
            v-model="form.description"
            id="description"
            placeholder="Describe your prediction and analysis..."
            rows="4"
            required
            maxlength="2000"
          ></textarea>
        </div>

        <div class="form-row">
          <div class="form-group">
            <label for="symbol">Token Symbol *</label>
            <input 
              v-model="form.symbol"
              id="symbol"
              type="text" 
              placeholder="e.g., BONK"
              required
            />
          </div>

          <div class="form-group">
            <label for="mint">Token Contract *</label>
            <input 
              v-model="form.mint"
              id="mint"
              type="text" 
              placeholder="Token mint address..."
              required
            />
          </div>
        </div>

        <div class="form-row">
          <div class="form-group">
            <label for="potential">Potential Growth % *</label>
            <input 
              v-model.number="form.potential"
              id="potential"
              type="number" 
              placeholder="100"
              min="1"
              max="10000"
              required
            />
          </div>

          <div class="form-group">
            <label for="price">Price (SOL)</label>
            <input 
              v-model.number="form.price"
              id="price"
              type="number" 
              placeholder="0.1"
              min="0"
              step="0.001"
            />
            <small>Leave 0 for free signal</small>
          </div>
        </div>

        <div class="modal-footer">
          <button type="button" @click="$emit('close')" class="cancel-btn">
            Cancel
          </button>
          <button type="submit" :disabled="loading" class="submit-btn">
            <i v-if="loading" class="fas fa-spinner fa-spin"></i>
            <i v-else class="fas fa-rocket"></i>
            {{ loading ? 'Creating...' : 'Create Signal' }}
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { ref, reactive } from 'vue'
import axios from 'axios'

export default {
  name: 'KolCreateModal',
  emits: ['close', 'created'],
  setup(props, { emit }) {
    const loading = ref(false)
    
    const form = reactive({
      title: '',
      description: '',
      symbol: '',
      mint: '',
      potential: null,
      price: 0
    })

    const handleOverlayClick = () => {
      emit('close')
    }

    const handleSubmit = async () => {
      loading.value = true
      
      try {
        const priceInLamports = form.price ? Math.round(form.price * 1e9) : 0
        
        const kolData = {
          title: form.title,
          description: form.description,
          symbol: form.symbol,
          mint: form.mint,
          potential: form.potential,
          price: priceInLamports
        }

        const response = await axios.post('/kol', kolData)
        
        if (response.data.success) {
          emit('created', response.data.data)
        }
      } catch (error) {
        console.error('Error creating KOL:', error)
        alert('Error creating signal. Please try again.')
      } finally {
        loading.value = false
      }
    }

    return {
      form,
      loading,
      handleOverlayClick,
      handleSubmit
    }
  }
}
</script>

