<template>
  <div class="kol-header">
    <div class="container">
      <div class="header-content">
        <div class="title-section">
          <h1>KOL Signals</h1>
          <p>Investment predictions from verified analysts</p>
        </div>

        <div class="actions-section">
          <div class="search-box">
            <i class="fas fa-search"></i>
            <input 
              v-model="searchInput"
              @input="handleSearch"
              type="text" 
              placeholder="Search signals..."
              class="search-input"
            />
          </div>

          <button 
            v-if="isAuthenticated"
            @click="$emit('create')" 
            class="create-btn"
          >
            <i class="fas fa-plus"></i>
            Create Signal
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue'
import store from '@/vuex'

export default {
  name: 'KolHeader',
  emits: ['search', 'create'],
  setup(props, { emit }) {
    const searchInput = ref('')
    
    const isAuthenticated = computed(() => store.getters.isAuthenticated)
    
    const handleSearch = () => {
      emit('search', searchInput.value)
    }

    return {
      searchInput,
      isAuthenticated,
      handleSearch
    }
  }
}
</script>

